import React, { useEffect, useState, useRef } from 'react'
import {connect} from 'react-redux'
import Controller from '../controller'
import cogoToast from 'cogo-toast'

const Game = connect(({token,playerid,id})=>({token,playerid,id}),dispatch=>({dispatch}))(({token,playerid,id,dispatch})=>{
    const ref = useRef()
    useEffect(()=>{
      Controller(ref.current,token,playerid)
    },[])
    const copyToClipboard = str => {
        const el = document.createElement('textarea');
        el.value = str;
        document.body.appendChild(el);
        el.select();
        document.execCommand('copy');
        document.body.removeChild(el);
        cogoToast.success('Copied!')
    };
    const url = `https://playunoonline.herokuapp.com/join/${id}`
    return (
      <div className="App">
        <div className='d-flex game' style={{alignItems:'center',height:'100%',width:'100%',position:'absolute'}}>
          <canvas ref={ref} className='bg-primary' style={{width:850,height:650,margin:'auto'}}></canvas>      
        </div>
        <div className='test' style={{zIndex:10000}}>
            Invite Link ➡️: <a href={url}>{url}</a> 
            <div href='/' className='border rounded d-inline ml-2' style={{padding:'5px',cursor:'pointer',zIndex:1000}} onClick={e=>{e.preventDefault();copyToClipboard(url)}}>Copy</div>
            <a href='/' onClick={e=>{e.preventDefault();dispatch({type:'LOG_OUT'})}} className='text-danger'>Log Out</a>
        </div>
      </div>
    );
})
export default Game